@import url(https://fonts.googleapis.com/css?family=Ubuntu);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Ubuntu', sans-serif;
    background: #f1f5f8;
    color: #222;
}

.booklist{
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    grid-gap: 2rem;
    gap: 2rem;
}

.book{
    background: #fff;
    border-radius: 1rem;
    padding: 1rem;
}

.book h1{
    margin-top: .5rem;
}
.book h4{
    color: #617d98;
    font-size: 0.75rem;
    margin-top: 0.25rem;
}
@media screen and (min-width: 768px){
    .booklist{
        grid-template-columns: repeat(3, 1fr);
    }
}
